.home{
    grid-template-columns: 4.32fr 7.68fr ;
    align-items: center;
    padding: 40px;
    height: 100vh;

}
.homeImg{
    height: 80vh;
    max-height: 100%;
    object-fit: cover;
    border-radius: 30px;
    box-shadow: 0 0 8px gray;
    z-index: 1;

}
.homeData{
    max-width: 600px;
    margin-inline: auto;
    padding-top: 20px;

}
.homeTitle{
    position: relative;
    font-size: var(--h1-font-size);
    line-height: 1.24;
    padding-left: 70px;
}
.homeTitle span{
    color: black;       
}

.homeTitle::before{
    content: '';
    position: absolute;
    left: 0;
    top: 22%;
    width: 40px;
    height: 5px;
    background-color: black;
}
.homeDescription{
    line-height: 2.125;
    margin-block: 16px 32px;

}
.colorBlock{
    position: fixed;
    height: 200%;
    width: 100%;
    background-color: var(--first-color);
    top: -50%;
    left: -83%;
    transform: rotate(-15deg);

}