.contactContainer{
     grid-template-columns: 4fr 8fr;

}
.contactTitle{
    font-size: var(--h3-font-size);
    font-weight: var(--weight-600);
    margin-bottom: 1rem;

}
.contactDescription{
    font-size: var(--small-font-size);
    line-height: 1.6;

}
.contactDescription , .contactInfo , .infoItem{
    margin-bottom: 20px;

}

.contactInfo .infoItem{
    display: flex;
    align-items: center;
    column-gap: 16px;
    cursor: pointer;


}

.infoItem:hover .infoIcon{
    transform: scale(1.1);

}


.contactInfo .infoIcon{
    color: var(--first-color);
    font-size: var(--h2-font-size);
}

.contactInfo .infoTitle{
    font-size: var(--small-font-size);

}

.contactInfo .infoDescription{
    font-family: var(--body-font);
    font-size: var(--small-font-size);
    font-weight: var(--weight-400);

}

.contactSocials{
    display: flex;
    column-gap: 16px;

}

.contactSocialLink{
    background-color: var(--container-color);
    color: var(--title-color);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 44px;
    transition: all 0.3s ease;
    align-items: center;
}

.contactSocialLink:hover{
    background-color: var(--first-color);
    color: var(--text-white);
    transform: scale(1.1);
}
.formInputGroup{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;

}
.formInputDiv{
     margin-bottom: 30px;

}

.formControl{
    border: 3px solid var(--container-color);
    background-color: var(--container-color);
    color: var(black);
    width: 100%;
    padding: 12px 24px;
    border-radius: 30px;
    font-size: var(--small-font-size);
    transition: all 0.5s ease;

}
.formControl:focus{
border-color: var(--title-color);
}

.textarea{
    resize: none;
    height: 160px   ;
}

.contactButtonIcon{
font-size: var(--h3-font-size);
line-height: 64px;

}